@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;

0,700;1,900&display=swap');

html {
  height: 100%;
}

body {
  margin-bottom: 80px;
  margin-top: 90px;
  font-family: "Menlo", "Meslo LG", monospace!important;
}

